/*
Default table styles

Use tables for tabular data. The base styling provides alignment and basic
spacing. Please reference the utilities section for helpful styling classes.

Markup:
<table>
  <caption>Superheroes Vs. real people</caption>
  <thead>
    <tr>
      <th>Superheroes</th>
      <th>Real people</th>
    </tr>
  </thead>
  <tbody>
    <tr>
      <td>Wonder Woman</td>
      <td>Ruth Bader Ginsburg</td>
    </tr>
    <tr>
      <td>Bat Girl</td>
      <td>Grace Hopper</td>
    </tr>
    <tr>
      <td>Thor</td>
      <td>Tina Fey</td>
    </tr>
  </tbody>
</table>

Weight: 8

Style guide Base - Tables
*/
/* Table variables */
table {
  border-collapse: collapse;
  border-spacing: 0;
  margin-top: 1.5em;
  margin-bottom: 1.5em;
  width: 100%;
}
td,
th,
caption {
  padding: 10px;
  text-align: left;
}
th {
  font-weight: bold;
}
/*
Default list styles

List styles are reset by default so that we can use them semantically in things
like lists of petition cards without having to override the browser styles each
time. Please reference the utilities section for helpful utilities to make lists
look like lists, e.g. bulleted, numbered, ruled.

Weight: 6

Style guide Base - Lists
*/
ol,
ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
}
/*
Default typography

The body sets its base font size at 16px with a line-height of 1.5. This sets
the foundation for our 24px vertical rhythm.

Weight: 5

Style guide Base - Typography
*/
body {
  font-family: "Helvetica Neue", Helvetica, Arial, Tahoma, sans-serif;
  font-size: 1rem;
  line-height: 1.5;
  color: #363135;
}
/*
Paragraphs

Paragraphs normalize margins so that margin is only applied if two or more
paragraphs exist as sibling elements. This allows you to use the `<p>` tag
freely without having to apply spacing overrides for margin.

Markup:
<p>
  Ulysses again glared at him and said, "Though you should give me all that you
  have in the world both now and all that you ever shall have, I will not stay
  my hand till I have paid all of you in full. You must fight, or fly for your
  lives; and fly, not a man of you shall."
</p>
<p>
  â€œMy friends, this man will give us no quarter. He will stand where he is and
  shoot us down till he has killed every man among us. Let us then show fight;
  draw your swords, and hold up the tables to shield you from his arrows. Let us
  have at him with a rush, to drive him from the pavement and doorway: we can
  then get through into the town, and raise such an alarm as shall soon stay
  his shooting.â€
</p>
<p>
  As he spoke he drew his keen blade of bronze, sharpened on both sides, and
  with a loud cry sprang towards Ulysses, but Ulysses instantly shot an arrow
  into his breast that caught him by the nipple and fixed itself in his liver.
  He dropped his sword and fell doubled up over his table. The cup and all the
  meats went over on to the ground as he smote the earth with his forehead in
  the agonies of death, and he kicked the stool with his feet until his eyes
  were closed in darkness.
</p>

Style guide Base - Typography - Paragraphs
*/
p {
  margin: 0;
}
p + p {
  margin-top: 1.5em;
}
/*
Superscripts and subscripts

Superscript and subscript elements are used very infrequently. Use them as
inline elements within a body of text.

Markup:
<p>Lorem ipsum dolor sit amet, <sup>consectetur</sup> adipiscing elit.
Vestibulum hendrerit dapibus nibh nec pretium. Curabitur dignissim est
ligula, nec mollis libero varius vitae.</p>
<p>Lorem ipsum dolor sit amet, <sub>adipiscing</sub> elit. Vestibulum
hendrerit dapibus nibh nec pretium. Curabitur dignissim est ligula, nec
mollis libero varius vitae.</p>

Style guide Base - Typography - Superscript/Subscript
*/
sup,
sub {
  font-size: 75%;
  line-height: 1.35;
}
