/*
Webfonts

We use a modified webfont called Change Calibre from
Klim Foundry's typeface [Calibre](https://klim.co.nz/retail-fonts/calibre/). We
make use of two weights and set those weights in the `@font-face` declaration.

Warning:
###### By default, we do not load Calibre right away.
This makes it easier to avoid the FOIT (flash of invisible text) that can happen
when a browser tries to use a font asset that hasn't been fully downloaded by
the client. In most of our apps, we use [Font Face Observer](https://github.com/bramstein/fontfaceobserver)
to asynchronously request the font and apply a utility class, `.type-loaded`
that sets the body to Calibre.

Weight: 3

Style guide Base - Webfonts
*/
/* Todo: Look into ways to making these urls protected against hotlinking */
@font-face {
  font-family: "Change Calibre";
  src: url("https://static.change.org/fonts/Change-Calibre-Regular.woff2") format("woff2"), url("https://static.change.org/fonts/Change-Calibre-Regular.woff") format("woff"), url("https://static.change.org/fonts/Change-Calibre-Regular.ttf.gz") format("truetype");
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-family: "Change Calibre";
  src: url("https://static.change.org/fonts/Change-Calibre-Bold.woff2") format("woff2"), url("https://static.change.org/fonts/Change-Calibre-Bold.woff") format("woff"), url("https://static.change.org/fonts/Change-Calibre-Bold.ttf.gz") format("truetype");
  font-weight: 700;
  font-style: normal;
}
/*
Internationalization

The Change.org apps support many languages â€” both latin and script. Our base
text stylings are set to latin typography, but in some cases, this creates poor
experiences in scripted languages. When encountering a base styling issue,
use the `|=` attribute selector to match language codes or subcodes on the `html`
element, e.g. lang|="hi" matches lang="hi" and lang="hi-IN".

For more details on attribute selectors, see the
[Mozilla documentation](https://developer.mozilla.org/en-US/docs/Web/CSS/Attribute_selectors).

Weight: 9

Style guide Base - Internationalization
*/
/*  Hindi words appear wrong when letter-spacing is applied */
html[lang|="hi"] * {
  letter-spacing: normal !important;
}
/*
Components

Components differ from utilities in that they represent an enclosed chunk of
HTML. In some ways, they are similar to a custom element, enclosing specific
semantics, styling, and behavior. Component driven development offers several
benefits when reading and writing HTML and CSS:

1. It keeps the specificity of selectors low.
2. It helps to decouple presentation semantics from document semantics.

Unlike utilities, components are much more robust. They can be extended by
adding modifiers, and may have descendent elements. A component modifier is a
class that modifies the presentation of the base component in some form. These
modifications often rely on the base presentation, so the class should be
included in the HTML in addition to the base component class.

Style guide Components
*/
/*
Animations

Generic animations can be found in animations.less. Each element that animates
requires the base class `.animate` to give it duration and base animation properties.
Applying a modifier defines the type of animation and starts the keyframes.

Markup:
<div class="animate {{modifier_class}}">
  Adding a modifier will animate me
</div>

.animate-fade-in-down - Fade in down
.animate-zoom-in      - Zoom in
.animate-pulse        - Pulse (e.g. for loading)

Style guide Components - Animations
*/
.animate {
  -webkit-animation-duration: 0.2s;
  animation-duration: 0.2s;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
}
@-webkit-keyframes animateWidth {
  0% {
    right: 100%;
    left: 0;
  }
  30% {
    right: 0;
    left: 0;
  }
  60% {
    right: 0;
    left: 0;
  }
  100% {
    left: 100%;
    right: 0;
  }
}
@keyframes animateWidth {
  0% {
    right: 100%;
    left: 0;
  }
  30% {
    right: 0;
    left: 0;
  }
  60% {
    right: 0;
    left: 0;
  }
  100% {
    left: 100%;
    right: 0;
  }
}
@-webkit-keyframes fadeInDown {
  0% {
    opacity: 0;
    -webkit-transform: translate3d(0, -50%, 0);
    transform: translate3d(0, -50%, 0);
  }
  100% {
    opacity: 1;
    -webkit-transform: none;
    transform: none;
  }
}
@keyframes fadeInDown {
  0% {
    opacity: 0;
    -webkit-transform: translate3d(0, -50%, 0);
    transform: translate3d(0, -50%, 0);
  }
  100% {
    opacity: 1;
    -webkit-transform: none;
    transform: none;
  }
}
.animate-fade-in-down {
  -webkit-animation-name: fadeInDown;
  animation-name: fadeInDown;
}
@-webkit-keyframes zoomIn {
  0% {
    opacity: 0;
    -webkit-transform: scale3d(0.9, 0.9, 0.7);
    transform: scale3d(0.9, 0.9, 0.7);
  }
  50% {
    opacity: 1;
  }
}
@keyframes zoomIn {
  0% {
    opacity: 0;
    -webkit-transform: scale3d(0.9, 0.9, 0.7);
    transform: scale3d(0.9, 0.9, 0.7);
  }
  50% {
    opacity: 1;
  }
}
.animate-zoom-in {
  -webkit-animation-name: zoomIn;
  animation-name: zoomIn;
  -webkit-animation-duration: 0.4s;
  animation-duration: 0.4s;
}
@-webkit-keyframes pulse {
  0% {
    opacity: 1;
  }
  60% {
    opacity: 0.5;
  }
  100% {
    opacity: 1;
  }
}
@keyframes pulse {
  0% {
    opacity: 1;
  }
  60% {
    opacity: 0.5;
  }
  100% {
    opacity: 1;
  }
}
