      document.getElementById('annotation-dropdown-menu').classList.toggle('animate-fade-in-down')">
  </button>
  <span>See what's new!</span>
  <div class="dropdown-menu">
    <div class="bubble bubble-outline-basic bubble-tl box-shadow animate" id="annotation-dropdown-menu">
      <h5>Annotations can help guide a user</h5>
      <p class="mvxxs">Call a user's attention to an element and give them supplementary guidance.</p>
      <button class="btn btn-text pln" onclick="
        document.getElementById('annotation-dropdown').classList.toggle('open');
