  <div class="arrange-fill">
    <p>
      Nulla vitae elit libero, a pharetra augue. Vivamus sagittis lacus vel
      augue laoreet rutrum faucibus dolor auctor.
    </p>
  </div>
  <div class="arrange-fit">
    <img src="http://placeimg.com/100/100/animals" />
  </div>
</div>

.arrange-top         - Align the expanded section to the top
.arrange-middle      - Align the expanded section to the middle
