/*
Flex embed

Flex embed allows responsive images and video to maintain their aspect ratio
at any screen resolution size. By default, the container uses 100% padding for
a 1:1 aspect ratio. Use the aspect ratio modifiers for non-square images.

Markup:
<div class="flex-embed">
  <div class="flex-embed-ratio {{modifier_class}}"></div>
  <div class="flex-embed-content">
    <img src="http://placeimg.com/1000/1000/arch" />
  </div>
</div>

.flex-embed-4x1    - 4 by 1 aspect ratio
.flex-embed-3x1    - 3 by 1 aspect ratio
.flex-embed-2x1    - 2 by 1 aspect ratio
.flex-embed-16x9   - 16 by 9 aspect ratio
.flex-embed-4x3    - 4 by 3 aspect ratio
.flex-embed-cinema - 2.35 by 1 aspect ratio

Style guideComponents - Flex embed
*/
.flex-embed {
  display: block;
  overflow: hidden;
  position: relative;
}
.flex-embed-ratio {
  display: block;
  padding-bottom: 100%;
  width: 100%;
}
.flex-embed-4x1 {
  padding-bottom: 25%;
}
.flex-embed-3x1 {
  padding-bottom: 33.33333333%;
}
.flex-embed-2x1 {
  padding-bottom: 50%;
}
.flex-embed-16x9 {
  padding-bottom: 56.25%;
}
.flex-embed-4x3 {
  padding-bottom: 75%;
}
.flex-embed-cinema {
  padding-bottom: 42.55319149%;
}
/* Fit the content to the aspect ratio */
.flex-embed-content,
.flex-embed-content iframe,
.flex-embed-content embed,
.flex-embed-content object,
.flex-embed-content video {
  bottom: 0;
  height: 100%;
  left: 0;
  position: absolute;
  top: 0;
  width: 100%;
}
/*
Flex embed cover images

You can use the cover-image modifier to maintain aspect ratio for cases where
you may need the image to be used as background.

Markup:
<div class="flex-embed flex-embed-cover-image" style="background-image: url('http://placeimg.com/1000/1000/arch')">
  <div class="flex-embed-ratio flex-embed-16x9 bg-brand type-faded"></div>
  <div class="flex-embed-content">
    <div class="position-middle">
      <div class="position-middle-item">
        <h1 class="type-brighter">Hello world</h1>
      </div>
    </div>
  </div>
</div>

Style guideComponents - Flex embed - Cover images
*/
.flex-embed-cover-image {
  background-position: 50%;
  background-repeat: no-repeat;
  background-size: cover;
  margin: 0 auto;
}
