Ghost UI

Ghost elements are used to connote parts of the UI that are still loading after
the initial page-load. They resemble the general layout of the component that is
loading in an abstract way. Use `.ghost-label` to suggest text, if needed,
and `.animate.animate-pulse` to add animation.

