/* Modal variables */
.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  overflow-x: hidden;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  outline: 0;
  /* Prevent Chrome on Windows from adding a focus outline. */
  z-index: 5000;
  text-align: center;
  background-color: rgba(157, 155, 156, 0.83);
  /* Filter the content behind to a hue of approximately 305Â°.
     This is only supported on Safari 9 and iOS for now. */
  -webkit-backdrop-filter: sepia(100%) hue-rotate(254deg);
  backdrop-filter: sepia(100%) hue-rotate(254deg);
}
.modal-dialog {
  background-color: #fff;
  position: absolute;
  left: 10px;
  right: 10px;
  margin-top: 10px;
  margin-bottom: 10px;
  text-align: left;
  padding: 40px 15px 15px;
  border-radius: 5px;
}
.modal-close {
  z-index: 5000;
}
@media (min-width: 768px) {
  .modal::before {
    content: "";
    display: inline-block;
    height: 100%;
    vertical-align: middle;
  }
  .modal-dialog {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    padding: 40px 30px 30px;
    width: 620px;
  }
  .modal-large {
    width: 720px;
  }
  .modal-small {
    width: 420px;
  }
}
@media (min-width: 992px) {
  .modal-large {
    width: 920px;
  }
}
