  </li>
  <li class="notification notification-with-counter">
    <span class="symbol symbol-notifications symbol-l"></span>
      <span class="notification-item">1</span>
  </li>
  <li class="notification notification-with-counter">
    <span class="symbol symbol-notifications symbol-l"></span>
      <span class="notification-item">10</span>
  </li>
  <li class="notification notification-with-counter">
    <span class="symbol symbol-notifications symbol-l"></span>
      <span class="notification-item">100</span>
  </li>
</ul>

.notification-with-counter - Notification with counter number

Style guideComponents - Notifications
*/
.notification {
