/*
  Rich text

  Ordered and unordered lists are reset by our scaffolding, so as to allow us
  more semantic markup when creating lists of elements, e.g. navigation or lists
  of petitions. Resetting gives us more versatility in the presentation without
  having to override the browser's defaults each time.

  However, there are times when we want to allow a section of HTML to behave as
  if it is default HTML and in cases where it would be impractical to manipulate
  the HTML in order to achieve the effect, e.g. user-generated petition stories
  that have bold/italic/links/lists allowed through a rich text editor. In these
  cases, make use of the `.rte` selector on a container of rich text, and lists
  will be transformed into readable, text lists.

  Markup:
  <div class="rte">
    <h6>Today's todo's</h6>
    <ol>
      <li>Wake up</li>
      <li>Brush teeth</li>
      <li>Binge watch Roseanne (even the seasons with the other Becky)</li>
      <li>Fall asleep</li>
    </ol>
    <h6>Grocery list</h6>
    <ul>
      <li>Candy</li>
      <li>Soda</li>
      <li>Pickle chips</li>
      <li>More pickle chips</li>
      <li>1 apple</li>
    </ul>
  </div>

  Style guideComponents - Rich text
*/
/* TODO: Better naming convention here */
.rte ul,
.rte ol {
  margin: 1.5em 3em;
