/*
Spinner

Use the loading spinner **sparingly** when you need to show
that more content is being loaded into the current page.

Markup:
<div class="spinner {{modifier_class}}">
  <div class="dot1"></div>
  <div class="dot2"></div>
  <div class="dot3"></div>
