.spinner-m    - Medium spinner
.spinner-xs   - Extra-small spinner

Style guideUtilities - Spinner
*/
.spinner {
  display: block;
  text-align: center;
}
.spinner > div,
.spinner-s > div {
  width: 10px;
  height: 10px;
  background-color: #737273;
  opacity: 0.25;
  border-radius: 100%;
  display: inline-block;
  -webkit-animation: glowdelay 1.4s infinite ease-in-out;
  animation: glowdelay 1.4s infinite ease-in-out;
}
.spinner > .dot2,
.spinner-s > .dot2 {
  margin: 0 4px;
}
.spinner-m > div {
  width: 15px;
  height: 15px;
}
.spinner-m > .dot2 {
  margin: 0 5px;
}
.spinner-xs > div {
  width: 5px;
  height: 5px;
}
.spinner-xs > .dot2 {
  margin: 0 3px;
}
.spinner-xxs > div {
  width: 2px;
  height: 2px;
}
.spinner-xxs > .dot2 {
  margin: 0 2px;
}
.spinner .dot1 {
  -webkit-animation-delay: -0.32s;
  animation-delay: -0.32s;
}
.spinner .dot2 {
  -webkit-animation-delay: -0.16s;
  animation-delay: -0.16s;
}
@-webkit-keyframes glowdelay {
  0%,
  100% {
    opacity: 0.25;
  }
  30% {
    opacity: 1;
  }
}
