/*
Symbols

Change.org's icons are contained within an icon font, `Change Symbols`. Symbols
inherit the current text color automatically, but can be modified with other
colors.

Markup:
<span class="symbol symbol-victory {{modifier_class}}"></span>
<span>Victory</span>

.symbol-round.bg-brand.type-brighter - Contain symbol within a circle. Requires a background and type color.
.symbol-outlined - Contain symbol within a circular outline.
.type-branded - Red symbol

Style guide Components - Symbols
*/
/* TODO: probably want to add a task to gzip the .ttf that's being encoded */
@font-face {
  font-family: "Change Symbols";
  font-weight: normal;
  font-style: normal;
  src: url("data:application/x-font-woff;charset=utf-8;base64,") format("woff"), url("data:application/font-sfnt;charset=utf-8;base64,") format("truetype");
}
/* Symbol variables */
.symbol {
  font-family: Change Symbols !important;
  font-weight: normal;
  font-style: normal;
  display: inline-block;
  line-height: inherit;
  /* So that icons line-height matches that of its parent */
  vertical-align: middle;
  /* Ensure the icon is rendered in the middle of text */
  margin-right: 0.625em;
  /* 10px at 16px base font size */
  text-decoration: none;
  text-transform: none !important;
  text-rendering: auto;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.symbol-round,
.symbol-outlined {
  /* Height and width are necessary to ensure icons are drawn as circles, since
  the icons have variable widths. */
  height: 2em;
  /* Give 0.5em vertical spacing around symbol */
  width: 2em;
  /* Give 0.5em horizontal spacing around symbol */
  line-height: 2;
  /* Center the alignment of the icons, */
  border-radius: 50%;
  text-align: center;
}
.symbol-outlined {
  border: 2px solid;
}
.symbol-outlined::before {
  position: relative;
  top: -2px;
}
/*
Symbol spacing

Symbols by default have margin to the right for use with text. You can use the
symbol margin modifiers to remove or flip the margins.

Markup:
<div>
  <span class="symbol symbol-victory"></span>
  <span>Default symbol</span>
</div>
<div class="mvxs">
  <span>Symbol after text</span>
  <span class="symbol symbol-victory symbol-after"></span>
</div>
<div>
  <span>No margin before</span>
  <span class="symbol symbol-victory symbol-compact"></span>
  <span>or after</span>
</div>

Style guide Components - Symbols - Spacing
*/
.symbol-after {
  margin-right: 0;
  margin-left: 0.35em;
}
.symbol-compact {
  margin: 0;
}
/*
Symbol sizes

Markup:
<span class="symbol symbol-victory {{modifier_class}}"></span>

.symbol-xs   - Extra small
.symbol-s    - Small
.symbol-m    - Medium
.symbol-l    - Large
.symbol-xl   - Extra large
.symbol-xxl  - XX Large
.symbol-xxxl - XXX Large

Style guide Components - Symbols - Sizes
*/
.symbol-xxs {
  font-size: 0.625rem !important;
}
.symbol-xs {
  font-size: 0.75rem !important;
}
.symbol-s {
  font-size: 0.875rem !important;
}
.symbol-m {
  font-size: 1rem !important;
}
.symbol-l {
  font-size: 1.125rem !important;
}
.symbol-xl {
  font-size: 1.375rem !important;
}
.symbol-xxl {
  font-size: 1.75rem !important;
}
.symbol-xxxl {
  font-size: 3rem !important;
}
/* Deprecated â€” Use .symbol-after instead */
.symbol-ext {
  margin-right: 0;
  margin-left: 0.35em;
}
/*
 Switch

 The switch is a more pronounced checkbox. When there is a binary decision that is more similar to a mode, and not a one-time selection, the switch can be used to call more attention to the input.

  Markup:
  <label class="switch" for="opt-in">
    <span class="switch-toggle">
      <input class="switch-toggle-input" type="checkbox" id="opt-in">
      <span class="switch-toggle-indicator">
        <span class="switch-on symbol symbol-check symbol-xs"></span>
        <span class="switch-off symbol symbol-close symbol-xs"></span>
      </span>
    </span>
    <span class="switch-label type-s type-weak">Turn on so petition coaches can contact you</span>
  </label>

  Style guideComponents - Switch
*/
.switch {
  cursor: pointer;
  font-size: 1rem;
  /* Force default value for relative spacing: */
  min-width: 100%;
  table-layout: auto;
}
.switch,
.switch-toggle-indicator {
  display: table;
  /* Use table for layout and vertical alignment */
}
.switch-label,
.switch-toggle,
.switch-on,
.switch-off {
  display: table-cell;
  vertical-align: middle;
}
.switch-label {
  width: 100%;
  padding-left: 0.875em;
}
.switch-toggle {
  position: relative;
  display: inline-block;
  vertical-align: top;
  width: 3em;
  height: 1.5em;
  border-radius: 1.25em;
  cursor: pointer;
}
.switch-toggle-input {
  position: absolute;
  opacity: 0;
}
.switch-toggle-indicator {
  background: #adacad;
  position: relative;
  height: inherit;
  border-radius: inherit;
  color: #fff;
  width: 100%;
}
.switch-on,
.switch-off {
  line-height: 1;
  /* crushes line-height for vertical alignment */
  text-align: center;
}
.switch-on {
  opacity: 0;
  padding-left: 0.25em;
}
.switch-off {
  opacity: 1;
  padding-right: 0.25em;
}
.switch-toggle-input:checked + .switch-toggle-indicator {
  background: #48c258;
}
.switch-toggle-input:checked + .switch-toggle-indicator .switch-on {
  opacity: 1;
}
.switch-toggle-input:checked + .switch-toggle-indicator .switch-off {
  opacity: 0;
}
.switch-toggle-indicator::after {
  content: "";
  position: absolute;
  width: 1.25em;
  height: 1.25em;
  border-radius: 100%;
  -webkit-box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
          box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  top: 0.125em;
  left: 0.125em;
  background: #fff;
  -webkit-transition: transform 0.2s ease-out;
  -webkit-transition: -webkit-transform 0.2s ease-out;
  transition: -webkit-transform 0.2s ease-out;
  -o-transition: transform 0.2s ease-out;
  transition: transform 0.2s ease-out;
  transition: transform 0.2s ease-out, -webkit-transform 0.2s ease-out;
}
.switch-input:checked ~ .switch-toggle-indicator {
  background: #48c258;
}
.switch-toggle-input:checked ~ .switch-toggle-indicator::after {
  -webkit-transform: translate3d(24px, 0, 0);
  transform: translate3d(24px, 0, 0);
}
/* Comes after symbols because switches override symbol display property */
/*
Tables

Use tables for tabular data.

Markup:
<table class="{{modifier_class}}">
  <caption class="table-caption">Superheroes Vs. real people</caption>
  <tr>
    <th class="table-header">Superheroes</th>
    <th class="table-header">Real people</th>
  </tr>
  <tr>
    <td>Wonder Woman</td>
    <td>Ruth Bader Ginsburg</td>
  </tr>
  <tr>
    <td>Bat Girl</td>
    <td>Grace Hopper</td>
  </tr>
  <tr>
    <td>Thor</td>
    <td>Tina Fey</td>
  </tr>
</table>

.table-stripe   - Zebra-striped table
.table-bordered - Bordered table

Style guide Components - Tables
*/
/* Table variables */
.table-stripe > tbody tr:nth-child(even) th,
.table-stripe > tbody tr:nth-child(even) td {
  background: #f6f4f6;
}
.table-stripe .table-caption {
  padding-top: 15px;
  padding-bottom: 15px;
  font-weight: bold;
  text-align: left;
  background: #fff;
  color: #363135;
}
.table-stripe .table-header {
  background: #dbd9db;
}
.table-bordered th,
.table-bordered td {
