*/
.tooltip {
  position: relative;
}
.tooltip::before,
.tooltip::after,
.tooltip::before,
.tooltip::after {
  bottom: 100%;
  left: 50%;
  opacity: 0;
  pointer-events: none;
  position: absolute;
  -webkit-transition: opacity 0.2s ease-out;
  -o-transition: opacity 0.2s ease-out;
  transition: opacity 0.2s ease-out;
  z-index: 1000;
}
.tooltip::before {
  background: rgba(54, 49, 53, 0.98);
  border-radius: 5px;
  color: #fff;
  content: attr(aria-label);
  font-size: 0.8rem;
  line-height: normal;
  margin-bottom: 9px;
  padding: 0.5em 1em;
  -webkit-transform: translate(-50%, 0);
  -ms-transform: translate(-50%, 0);
      transform: translate(-50%, 0);
  white-space: nowrap;
}
.tooltip::after {
  content: "";
  margin-left: -6px;
  border-width: 6px 6px 0;
  border-style: solid;
  border-color: rgba(54, 49, 53, 0.98) transparent;
  margin-bottom: 3px;
}
.tooltip:hover::before,
.tooltip:focus::before,
.tooltip:hover::after,
.tooltip:focus::after {
  opacity: 1;
}
/*
Tooltip alignment

Sometimes the element with a tooltip will be flush against the left or right
of the window. Centering the tooltip could cause issues where the tooltip
renders off the screen. In these cases, you'll want to align the tooltip with
the element by aligning on the left or right side of the element.

Warning:
###### Cannot combine with `.tooltip-before` and `.tooltip-after`
`.tooltip-before` and `.tooltip-after` will override the the alignment
modifiers since they are already forcing the left and right values of the
tooltip to appear before or after their targets.

Markup:
<div class="txt-l">
  <div>
    <span>Tooltips can be aligned left, above.</span>
    <button class="tooltip tooltip-align-l" aria-label="left, adjective | \left\, located to the west of a subject facing north">
      <span class="symbol symbol-info symbol-compact"></span>
    </button>
  </div>
  <div>
    <span>Tooltips can be aligned left, below.</span>
    <button class="tooltip tooltip-below tooltip-align-l" aria-label="left, adjective | \left\, located to the west of a subject facing north">
      <span class="symbol symbol-info symbol-compact"></span>
    </button>
  </div>
</div>
<div class="txt-r">
  <div>
    <span>Tooltips can be aligned right, above.</span>
    <button class="tooltip tooltip-align-r" aria-label="right, adjective | \rahy\, located to the east of a subject facing north">
      <span class="symbol symbol-info symbol-compact"></span>
    </button>
  </div>
  <div>
    <span>Tooltips can be aligned right, below.</span>
    <button class="tooltip tooltip-below tooltip-align-r" aria-label="right, adjective | \rahy\, located to the east of a subject facing north">
      <span class="symbol symbol-info symbol-compact"></span>
    </button>
  </div>
</div>

Weight: +1

Style guideComponents - Tooltips - Tooltip alignment
