}
.tooltip-align-l::before {
  left: -1em;
}
.tooltip-align-r::before {
  left: auto;
  right: -1em;
}
/*
  Tooltip positioning

  Tooltips can be displayed on top of, after, below, or before their target
  element. This can be helpful if you want to esure the tooltip doesn't cover
  important information.

  Markup:
  <div>
    <button class="btn tooltip tooltip-after" aria-label="You got it, dude.">
      Show tooltip after
    </button>
  </div>
  <div class="txt-c">
    <button class="btn tooltip tooltip-below" aria-label="You got it, dude.">
      Show tooltip below
    </button>
  </div>
  <div class="txt-r">
    <button class="btn tooltip tooltip-before" aria-label="You got it, dude.">
      Show tooltip before
    </button>
  </div>

