/* Position after element */
.tooltip-after::before,
.tooltip-after::after {
  left: 100%;
}
.tooltip-after::before {
  margin-left: 9px;
}
.tooltip-after::after {
  border-left-width: 0;
  margin-left: 3px;
}
/* Position below element */
.tooltip-below::before,
.tooltip-below::after {
  top: 100%;
}
.tooltip-below::before {
  margin-top: 9px;
}
.tooltip-below::after {
  border-top-width: 0;
  border-bottom-width: 6px;
  margin-top: 3px;
}
/* Position before element */
.tooltip-before::before,
.tooltip-before::after {
  left: auto;
  right: 100%;
}
.tooltip-before::before {
  margin-right: 9px;
}
.tooltip-before::after {
  border-right-width: 0;
  margin-left: 0;
  margin-right: 3px;
}
/* Positioning modifiers shared properties */
.tooltip-after::before,
.tooltip-after::after,
.tooltip-before::before,
.tooltip-before::after,
.tooltip-below::before,
.tooltip-below::after {
  bottom: auto;
}
.tooltip-after::before,
.tooltip-after::after,
.tooltip-before::before,
.tooltip-before::after {
  top: 50%;
}
.tooltip-after::before,
.tooltip-before::before,
.tooltip-below::after {
  margin-bottom: 0;
}
.tooltip-after::before,
.tooltip-before::before {
  -webkit-transform: translate(0, -50%);
  -ms-transform: translate(0, -50%);
      transform: translate(0, -50%);
