/*
  Tooltip sizes

  The default tooltips are limited to one line to encourage concise context.
  However, in some cases, wrapping the text is desirable. To wrap the text, a
  width needs to be declared on the tooltip. There are four sizes to choose
  from: small, medium, large, and full-width.

  Markup:
  <div class="mbs">
    <button class="btn tooltip tooltip-s"
      aria-label="It's a world of laughter, a world of tears;
      From time to time, the clouds give rest to the moon beholders">
      <span>Small tooltip</span>
    </button>
  </div>
  <div class="mvs">
    <button class="btn tooltip tooltip-m"
      aria-label="It's a world of laughter, a world of tears;
      From time to time, the clouds give rest to the moon beholders">
      <span>Medium tooltip</span>
    </button>
  </div>
  <div class="mvs">
    <button class="btn tooltip tooltip-l"
      aria-label="It's a world of laughter, a world of tears;
      From time to time, the clouds give rest to the moon beholders">
      <span>Large tooltip</span>
    </button>
  </div>
  <div class="mts">
    <button class="btn tooltip tooltip-full"
      aria-label="It's a world of laughter, a world of tears;
      From time to time, the clouds give rest to the moon beholders">
      <span>Tooltips can also be full-width</span>
    </button>
  </div>

  Weight: +2

  Style guideComponents - Tooltips - Sizes
*/
.tooltip-s::before,
.tooltip-m::before,
.tooltip-l::before,
.tooltip-full::before {
  white-space: normal;
  text-align: left;
}
.tooltip-s::before {
  width: 10em;
}
.tooltip-m::before {
  width: 15em;
}
.tooltip-l::before {
  width: 20em;
}
.tooltip-full::before {
  width: 100%;
}
@media (max-width: 767px) {
