/*
  Truncate

  Truncate long blocks of text without requiring any javascript libraries. This
  implementation relies on a consistent vertical rhythm in order to maintain an
  appropriate overflow since the height of the truncate component is calculated
  using the 24 pixel baseline grid.

  Markup:
  <div class="truncate {{modifier_class}}">
    <div class="truncate-body">
      <div class="truncate-body-text">
        <p>
          Call me Ishmael. Some years ago- never mind how long precisely- having
          little or no money in my purse, and nothing particular to interest me
          on shore, I thought I would sail about a little and see the watery part
          of the world. It is a way I have of driving off the spleen and
          regulating the circulation. Whenever I find myself growing grim about
          the mouth; whenever it is a damp, drizzly November in my soul; whenever
          I find myself involuntarily pausing before coffin warehouses, and
          bringing up the rear of every funeral I meet; and especially whenever
          my hypos get such an upper hand of me, that it requires a strong moral
          principle to prevent me from deliberately stepping into the street, and
          methodically knocking people's hats off- then, I account it high time to
          get to sea as soon as I can. This is my substitute for pistol and ball.
          With a philosophical flourish Cato throws himself upon his sword; I
          quietly take to the ship. There is nothing surprising in this. If they
          but knew it, almost all men in their degree, some time or other, cherish
          very nearly the same feelings towards the ocean with me.
        </p>
      </div>
    </div>
  </div>

  .truncate-2-lines  - truncate to 2 lines
  .truncate-5-lines  - truncate to 5 lines

  Style guideComponents - Truncate
*/
/* Truncate vars */
.truncate-body {
  overflow: hidden;
  height: 4.5em;
  /* Base height for the component set at 3 lines */
  padding-right: 1em;
  /* Give room for the ellipsis */
  text-align: justify;
  /* Justifies except last line */
}
.truncate-body::before {
  content: "";
  float: left;
  /* Sets the height and width so that the ellipsis will only clear if text is
  longer than the defined height of the truncate component */
  height: 100%;
  width: 0.2em;
}
.truncate-body::after,
.truncate-body-text {
  float: right;
  /* Floated so that the ellipsis can clear when text overflows */
}
.truncate-body::after {
  -webkit-box-sizing: content-box;
          box-sizing: content-box;
  content: "\02026";
  /* Display an ellipsis character */
  left: 100%;
  /* Push the object to the right */
  margin-right: -1em;
  /* Keep the text selectable by pulling the ellipsis off */
  padding-right: 0.2em;
  /* Push it back 1px to account for the 1px spacer */
  position: relative;
  /* Create positioning context for top and left properties */
  text-align: left;
  top: -1.5em;
  /* Pulls the cleared object up on top of the truncated text */
  width: 1em;
}
.truncate-body p {
  /* Forces text to be inline so that paragraphs that ending 1 line before the
  truncation don't produce a blank line. */
  display: inline;
}
.truncate-body-text {
  width: 100%;
  margin-left: -0.2em;
  /* Pull back 1px on top of the 1px spacer */
}
/* Action text for CTA such as Read more */
.truncate-action {
  margin: 0.75em 0;
  /* top/bottom margin of 1 unit of vertical spacing */
}
/* Line number modifiers */
.truncate-2-lines .truncate-body {
  height: 3em;
}
.truncate-5-lines .truncate-body {
  height: 7.5em;
}
/*
  Truncate with toggle

  Sometimes instead of linking to another page, you may want to expose the
  truncated text right there, inline. To do this, you'll need to add some extra
  elements: an input to create a switch; and a label for the input that contains
  the call to action.

  Markup:
  <div class="truncate truncate-5-lines">
    <input class="truncate-toggle" type="checkbox" id="truncate-example" />
    <div class="truncate-body">
      <div class="truncate-body-text">
        <p>
          It was seven o'clock of a very warm evening in the Seeonee hills when
          Father Wolf woke up from his day's rest, scratched himself, yawned,
          and spread out his paws one after the other to get rid of the sleepy
          feeling in their tips. Mother Wolf lay with her big gray nose dropped
          across her four tumbling, squealing cubs, and the moon shone into the
          mouth of the cave where they all lived. "Augrh!" said Father Wolf.
          "It is time to hunt again." He was going to spring down hill when a
          little shadow with a bushy tail crossed the threshold and whined:
          "Good luck go with you, O Chief of the Wolves. And good luck and
          strong white teeth go with noble children that they may never forget
          the hungry in this world."
        </p>
        <p>
          It was the jackalâ€”Tabaqui, the Dish-lickerâ€”and the wolves of India
          despise Tabaqui because he runs about making mischief, and telling
          tales, and eating rags and pieces of leather from the village
          rubbish-heaps. But they are afraid of him too, because Tabaqui, more
          than anyone else in the jungle, is apt to go mad, and then he forgets
          that he was ever afraid of anyone, and runs through the forest biting
          everything in his way. Even the tiger runs and hides when little
          Tabaqui goes mad, for madness is the most disgraceful thing that can
          overtake a wild creature. We call it hydrophobia, but they call it
          dewaneeâ€”the madnessâ€”and run.
        </p>
        <p>
          "Enter, then, and look," said Father Wolf stiffly, "but there is no
          food here."
        </p>
      </div>
    </div>
    <label for="truncate-example" class="truncate-action link">
      <span class="truncate-action-more">View more</span>
      <span class="truncate-action-less">View less</span>
    </label>
  </div>

  Style guide Components - Truncate - Truncate with toggle
*/
.truncate-toggle,
.truncate-toggle:checked ~ .truncate-action .truncate-action-more,
.truncate-action-less {
  display: none;
}
.truncate-toggle:checked + .truncate-body {
  height: auto;
  max-height: none;
  /* for .truncate-relative */
  overflow: auto;
}
.truncate-toggle:checked ~ .truncate-action .truncate-action-less,
.truncate-toggle:checked ~ .truncate-body p {
  display: block;
}
/*
  Relative height truncation

  In rare instances, truncation needs to be applied to elements that normally
  are displayed fully, but when reaching a maximum height, will need to be
  truncated. In these cases, you can use `.truncate-relative` in order to allow
  the height of the truncated element to be dynamic.

  Warning:
  ###### Do not combine with other truncate modifiers
  `.truncate-relative` cannot be combined with `.truncate-n-lines` modifiers.
  Instead of an ellipsis, `.truncate-relative` reveals `.truncate-action` when the
  text overflows. In order to hide `.truncate-action` when the text doesn't
  overflow, a pseudo element is used to "cover" the action by using background
  color. This limits usage to content on white backgrounds. The space for
  `.truncate-action` is in the layout even when the text doesn't overflow. This
  results in a slightly different bottom spacing for text that is fully visible
  vs truncated.

  Markup:
  <div class="truncate truncate-relative mbs">
    <input class="truncate-toggle" type="checkbox" id="truncate-relative-toggle" />
    <div class="truncate-body" id="truncate-overflow-container">
      <div class="truncate-body-text">
        <p id="truncate-relative-example-text">
          Dark spruce forest frowned on either side the frozen waterway. The
          trees had been stripped by a recent wind of their white covering of
          frost, and they seemed to lean towards each other, black and ominous,
          in the fading light. A vast silence reigned over the land. The land
          itself was a desolation, lifeless, without movement, so lone and cold
          that the spirit of it was not even that of sadness.
        </p>
      </div>
      <label class="truncate-action type-weak" for="truncate-relative-toggle">
        <span class="link link-subtle type-s">Read more</span>
      </label>
    </div>
  </div>
  <button class="btn btn-action" id="truncate-add-example-text">
    Add text
  </button>

  Weight: +1

  Style guide Components - Truncate - Relative height
*/
.truncate-relative .truncate-body {
  -webkit-box-sizing: content-box;
          box-sizing: content-box;
  height: auto;
  max-height: 15em;
  position: relative;
  /* Give space for focus state on .truncate-action */
  margin: 0 -1em;
  padding-bottom: 1.7em;
  padding-left: 1em;
}
.truncate-relative .truncate-body::before {
  content: none;
  /* remove standard truncation */
}
.truncate-relative .truncate-body::after,
.truncate-relative .truncate-body-text {
  float: none;
  margin: 0;
}
.truncate-relative .truncate-body::after,
.truncate-relative .truncate-action {
  background: #fff;
  left: 1em;
  position: absolute;
  right: 1em;
  width: auto;
}
.truncate-relative .truncate-body::after {
  content: "";
  height: 1.5em;
  padding: 0;
  top: auto;
}
.truncate-relative .truncate-action {
  bottom: 0.2em;
  /* Compensate for spacer so focus state shows */
  margin: 0;
  text-align: left;
}
