  display: block;
  color: inherit;
  text-decoration: none;
}
.link-block:hover {
  color: inherit;
  text-decoration: none;
}
.link-block:hover .link {
  color: #ec2c22;
}
.link-block:hover .link:hover .type-weak {
  text-decoration: underline;
}
.link-block:hover .link-inverted {
  color: #fff;
  text-decoration: underline;
}
